  font-size: 16px;
  color: #666;
  line-height: 1.5;
  text-align: center;
  margin: 20px 0;
}
.onboarding-container .slider-container {
  width: 100%;
  height: 50px;
  background-color: #eee;
  border-radius: 25px;
  position: relative;
  overflow: hidden;
}
.onboarding-container .slider-container .slider {
  width: 200%;
  height: 100%;
  background: linear-gradient(to right, #4caf50 50%, #f44336 50%);
  display: flex;
  transition: transform 0.3s ease;
}
.onboarding-container .slider-container:hover .slider {
  transform: translateX(-50%);
}
.onboarding-container .buttons-container {
  display: flex;
  justify-content: space-between;
  gap: 10px;
}
.onboarding-container .buttons-container .btn-white-green {
  background-color: white;
  color: #4D861F;
  border: 2px solid #4D861F;
  padding: 10px 20px;
  border-radius: 25px;
  font-weight: bold;
  text-align: center;
  cursor: pointer;
  transition: background-color 0.3s;
}
.onboarding-container .buttons-container .btn-white-green:hover {
  background-color: #4D861F;
  color: white;
}
.onboarding-container .buttons-container .btn-green-white {
