  font-weight: bold;
  text-align: center;
  cursor: pointer;
  transition: background-color 0.3s;
}
.onboarding-container .buttons-container .btn-green-white:hover {
  background-color: #4D861F;
}

footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
