  align-items: center;
  color: grey;
  text-decoration: none;
  font-size: 16px;
  transition: color 0.3s ease;
}
footer i {
  font-size: 24px;
  margin-bottom: 5px;
}
footer:hover {
  color: #4D861F;
}
footer:focus {
  color: #4D861F;
